
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState, mapGetters } from "vuex";

const list = () => ["最新动态", "统计分析"];
export default {
components: {
XHeader,
Tab,
TabItem
},
computed: {
...mapGetters(["lessonPopup"])
},
data() {
return {
list: list(),
index: 0,
selected: ""
};
},
watch: {
$route(to, from) {
if (to.query.status == "dynamic") {
this.selected = "最新动态";
}
}
},
created() {
let currentSelected = this.$route.query.status;
if (currentSelected === "dynamic") {
this.selected = "最新动态";
} else if (currentSelected === "statistics") {
this.selected = "统计分析";
}
},
methods: {
handler(item) {
this.selected = item;
let path = this.$route.path;
if (item === "最新动态") {
this.$router.replace(`${path}?status=dynamic`);
} else if (item === "统计分析") {
this.$router.replace(`${path}?status=statistics`);
}
},
changChoose() {
this.$store.dispatch("updateLessonPopup", {
lessonPopup: true
});
}
}
};



